module $ { export class $mol_clicker extends $mol_viewer {

	/// tagName \button
	tagName() {
		return "button"
	}

	/// enabled : true
	enabled() {
		return ( <any> true )
	}

	/// eventClick : null
	@ $mol_prop()
	eventClick( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : ( <any> null )
	}

	/// event * click > eventClick
	event( key : any , ...diff : any[] ) {
		switch( key ){
			case "click" : return this.eventClick(  ...diff )
		}
		return <any>null
	}

	event_keys(){
		return <string[]>["click"]
	}

	/// disabled : false
	disabled() {
		return ( <any> false )
	}

	/// attr * disabled < disabled
	attr( key : any ) {
		switch( key ){
			case "disabled" : return this.disabled()
		}
		return <any>null
	}

	attr_keys(){
		return <string[]>["disabled"]
	}

} }

