module $ { export class $mol_scroller extends $mol_viewer {

	/// scrollTop 0
	scrollTop() {
		return 0
	}

	/// scrollLeft 0
	scrollLeft() {
		return 0
	}

	/// field * 
	/// 	scrollTop < scrollTop 
	/// 	scrollLeft < scrollLeft
	field( key : any ) {
		switch( key ){
			case "scrollTop" : return this.scrollTop()
			case "scrollLeft" : return this.scrollLeft()
		}
		return <any>null
	}

	field_keys(){
		return <string[]>["scrollTop","scrollLeft"]
	}

	/// eventScroll null
	@ $mol_prop()
	eventScroll( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : <any> null
	}

	/// event * 
	/// 	scroll > eventScroll 
	/// 	overflow > eventScroll 
	/// 	underflow > eventScroll 
	/// 	- wheel > eventWheel
	event( key : any , ...diff : any[] ) {
		switch( key ){
			case "scroll" : return this.eventScroll(  ...diff )
			case "overflow" : return this.eventScroll(  ...diff )
			case "underflow" : return this.eventScroll(  ...diff )
		}
		return <any>null
	}

	event_keys(){
		return <string[]>["scroll","overflow","underflow"]
	}

} }

